import PropTypes from "prop-types";
import { useState } from "react";
// @mui
import { useTheme } from "@mui/material/styles";
import {
  Checkbox,
  TableRow,
  TableCell,
  MenuItem,
} from "@mui/material";
// utils
import { fDateTime } from "../../../../utils/formatTime";
import { fCurrency } from "../../../../utils/formatNumber";
// components
import Label from "../../../../components/Label";
import Iconify from "../../../../components/Iconify";
import { TableMoreMenu } from "../../../../components/table";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

TicketTableRow.propTypes = {
  row: PropTypes.object.isRequired,
  selected: PropTypes.bool,
  onSelectRow: PropTypes.func,
  onViewRow: PropTypes.func,
};

export default function TicketTableRow({
  row,
  selected,
  onSelectRow,
  onViewRow,
}) {
  const theme = useTheme();

  const [openMenu, setOpenMenuActions] = useState(null);

  const handleOpenMenu = (event) => {
    setOpenMenuActions(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpenMenuActions(null);
  };

  const { t } = useTranslation();

  return (
    <TableRow hover selected={selected}>
      <TableCell padding="checkbox">
        <Checkbox checked={selected} onClick={onSelectRow} />
      </TableCell>
      <TableCell padding="checkbox" />
      <TableCell align="center">{row?.ref_code}</TableCell>
      <TableCell align="center">{row?.transaction_id}</TableCell>

      <TableCell align="left">{row?.full_name || "-"}</TableCell>
      <TableCell align="center">{fDateTime(row?.created_on)}</TableCell>
      <TableCell align="center">{row?.operator_name}</TableCell>
      <TableCell align="center">{row?.phone}</TableCell>
      <TableCell align="center">{fCurrency(row?.amount)}</TableCell>
      <TableCell align="center">{fCurrency(row?.percent)}</TableCell>
      <TableCell align="center">{fCurrency(row?.sp_percent)}</TableCell>

      <TableCell align="center">
        <Iconify
          icon={
            row.transaction_status === "COMPLETE"
              ? "eva:checkmark-circle-fill"
              : row.transaction_status === "PENDING"
              ? "eva:clock-outline"
              : row.transaction_status === "FAILED"
              ? "eva:close-circle-fill"
              : ""
          }
          sx={{
            width: 20,
            height: 20,
            color: "success.main",
            ...(row.transaction_status === "PENDING" && {
              color: "warning.main",
            }),
            ...(row.transaction_status === "FAILED" && { color: "error.main" }),
          }}
        />
      </TableCell>

      <TableCell align="right">
        <TableMoreMenu
          open={openMenu}
          onOpen={handleOpenMenu}
          onClose={handleCloseMenu}
          actions={
            <>
              <MenuItem
                onClick={() => {
                  onViewRow();
                  handleCloseMenu();
                }}
              >
                <Iconify icon={"eva:eye-fill"} />
                {t("field.view")}
              </MenuItem>
            </>
          }
        />
      </TableCell>
    </TableRow>
  );
}
